import { TO_FAVORITE, TO_BASKET } from '../../action-names/action-names';

//    - ,        .
//  type -  , product -         
// actionType -  .  true,            .  false,         .

export default function basketAndFavorite(state = { favorite: [], basket: [] }, { type, product, actionType }) {
  switch (type) {
    case TO_FAVORITE:
      return actionType ? { ...state, favorite: state.favorite.filter((prod) => prod.id !== product.id) } : { ...state, favorite: [].concat(state.favorite, product) };
    case TO_BASKET:
      return actionType ? { ...state, basket: state.basket.filter((prod) => prod.id !== product.id) } : { ...state, basket: [].concat(state.basket, product) };
    default:
      return state;
  }
}
